@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(48, 47, 47);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4, h5
{
  color: #ee344e;
}

a
{
  color: gray;
}
ul
{
  padding: 0;
}
li
{
  list-style: none;
}
.App_mainContainer__2o5r7
{
    width: 95vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-areas: "photo contact contact contact contact"
                         "languages other other other other"
                         "languages work work work work"
                         "languages extras extras extras extras"
                         "lifeline lifeline lifeline lifeline lifeline"
                         "separator separator separator separator separator"
                         "planguages planguages dbases dbases dbases"
                         "backend backend frontend frontend frontend";
}
